import { Component, OnInit , OnDestroy} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Http, Response, RequestOptions, ResponseContentType , RequestMethod, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Router} from '@angular/router';


@Component({
  selector: 'app-standingscreen',
  templateUrl: './standingscreen.component.html',
  styleUrls: ['./standingscreen.component.css']
})
export class StandingscreenComponent implements OnInit, OnDestroy {
  id: number;
  private sub: any;
selectionlist: any;
selectionlist1 = [];

  constructor(private route: ActivatedRoute, private http: Http, private router: Router) {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id'];
    });
      const headers = new Headers({'X-Auth-Token': '660e1384580046548f6d44ad531f3919'});
      const options = new RequestOptions({ headers: headers });
  this.http.get('https://api.football-data.org/v1/competitions/' + this.id + '/leagueTable', options)
  .map((res: Response) => JSON.parse('[' + res['_body']  + ']'))
  .subscribe(data => this.selectionlist = data, null, () => {

  Array.prototype.forEach.call(this.selectionlist[0].standing, child => this.selectionlist1.push(child));

  });
  }

  ngOnInit() {

  }

  clicked(link: string) {
   const temp = link.split('/');
   const temp1 = temp[temp.length - 1];
this.router.navigate(['/teamscreen', temp1]);
  }
  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



