import { Component, OnInit , OnDestroy} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Http, Response, RequestOptions, ResponseContentType , RequestMethod, Headers} from '@angular/http';
import 'rxjs/add/operator/map';


@Component({
  selector: 'app-team-screen',
  templateUrl: './team-screen.component.html',
  styleUrls: ['./team-screen.component.css']
})
export class TeamScreenComponent implements OnInit, OnDestroy {

  id: number;
  private sub: any;
Teamlist: any;
Teamlist1 = [];
fixturelist: any;
fixturelist1 = [];

  constructor(private route: ActivatedRoute, private http: Http) {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id'];
    });
      const headers = new Headers({'X-Auth-Token': '660e1384580046548f6d44ad531f3919'});
      const options = new RequestOptions({ headers: headers });
  this.http.get('https://api.football-data.org/v1/teams/' + this.id + '/players', options)
  .map((res: Response) => JSON.parse('[' + res['_body']  + ']'))
  .subscribe(data => this.Teamlist = data, null, () => {console.log(this.Teamlist);
  Array.prototype.forEach.call(this.Teamlist[0].players, child => this.Teamlist1.push(child));
  });

  this.http.get('https://api.football-data.org/v1/teams/' + this.id + '/fixtures', options)
  .map((res: Response) => JSON.parse('[' + res['_body']  + ']'))
  .subscribe(data => this.fixturelist = data, null, () => {console.log(this.fixturelist);
  Array.prototype.forEach.call(this.fixturelist[0].fixtures , child =>{
    if(child.status!='FINISHED' && child.status != 'POSTPONED')
    this.fixturelist1.push(child)
  });
  });
  }

  ngOnInit() {

  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



