import { Component, OnInit } from '@angular/core';
import { Http, Response, RequestOptions, ResponseContentType , RequestMethod, Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-competition-list',
  templateUrl: './competition-list.component.html',
  styleUrls: ['./competition-list.component.css']
})
export class CompetitionListComponent implements OnInit {
CompetitionList: any;
CompetitionList1 = [];

  constructor(private http: Http) {
    const headers = new Headers({'X-Auth-Token': '660e1384580046548f6d44ad531f3919'});
    const options = new RequestOptions({ headers: headers });
this.http.get('https://api.football-data.org/v1/competitions', options)

.map((res: Response) => res['_body'])


.subscribe(data => this.CompetitionList = data, null, () => {
Array.prototype.forEach.call(JSON.parse(this.CompetitionList), child => this.CompetitionList1.push(child));
});

  }


  ngOnInit() {

  }

}



